<template>
  <div>
    <!-- <section> -->
    <div
      class="zmb_card"
      v-for="(item,index) in list.list"
      :key="index"
      @click.stop="onteacher(item.teacher_id)"
    >
      <img :src="item.avatar" alt="" />
      <div class="zmb_txt">
        <p>{{ item.teacher_name }}</p>
        <p>男 30年教龄</p>
      </div>
      <div class="zmb_guanzhu">
        <button class="zmb_guanzhu_btn" @click.stop="onfollow(item.teacher_id)">不再关注</button>
      </div>
    </div>
    <!-- </section> -->
  </div>
</template>

<script scoped>
export default {
  data() {
    return {
      list: [],
      flag: 0,
    };
  },
  mounted() {
    this.getdata();
  },
  methods: {
    getdata() {
      this.$APP.fllowteacher().then((res) => {
        console.log(res.data.data);
        this.list = res.data.data;
      });
    },
    //点击进入老师详情
    onteacher(id) {
      this.$router.push({
        path: "/teacherXQ",
        query: { id: id },
      });
    },
    //取消关注
    onfollow(id) {
      this.$APP.fllow(id).then((res) => {
        console.log(res.data.data.flag);
        this.flag = res.data.data.flag;
        this.getdata();
      });
    },
  },
};
</script>

<style>
body {
  background: rgb(245, 245, 245);
}
.zmb_card {
  font-size: 0.5rem;
  width: 95%;
  height: 4rem;
  margin: 0.3rem auto 0;
  background: white;
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 1rem;
  box-sizing: border-box;
}
.zmb_card img {
  width: 2.5rem;
  height: 2.5rem;
  margin-right: 0.5rem;
}
.zmb_txt {
  flex: 1;
}
.zmb_guanzhu_btn {
  width: 3rem;
  height: 1rem;
  border: 1px solid #eb6100;
  border-radius: 1rem;
}
</style>